import React, { Component } from 'react'
import { Row, Col, Button } from 'react-bootstrap';
import ImageType from '../../CommonComponents/MediaType/ImageType';
import { connect } from 'react-redux';
import { REMOVE_TAGGED_PRODUCT_ACTION } from '../../../../../actions/moderationActions';
import { FontAwesomeIcon } from "../../../../../Assets/AssetsHelper";
import { checkPermission } from '../../../../../actions/collaboratorActions';

class ProductTags extends Component {
    removeTaggProduct = (product_sku, productId, referenceId) => { if(checkPermission('contentGallery', 'editGallery', true)) {this.props.REMOVE_TAGGED_PRODUCT_ACTION(product_sku, productId, referenceId, this.props.navigate); }};

    render() {
        const { item, listView , index, openContent} = this.props;
        return (item.postproduct && item.postproduct.length > 0 ?
                <Row className='d-flex gx-2 flex-nowrap product-scroll'>
                    {item.postproduct.map((item, index) => {
                        return <Col xs={2} key={index} style={{width:'20%'}}>
                            <div className='w-100 position-relative'>
                                <ImageType mediaClass="rounded-1 overflow-hidden position-relative bg-body" size={100} url={item?.product?.product_image} height={50} width={50} alt={''} isCover={true} />
                                <span className='h-16px w-16px rounded-circle bg-danger cursor-pointer position-absolute f-center text-white fs-10' style={{ top: -6, right: -6 }} onClick={() => this.removeTaggProduct(item.product.product_sku, item.product.id, item.post_id)}>
                                <FontAwesomeIcon icon={'xmark'} />
                                </span>
                            </div>
                        </Col>
                    })}
                    <Col xs={2} style={{width:'20%'}}>
                        <Button variant='light-primary' className='f-center flex-shrink-0 p-0 h-100 w-100 fs-6' onClick={()=>openContent(item, index)} disabled={localStorage.getItem('wallLimitExceed') == 'true' ? true : false}>
                            <FontAwesomeIcon icon={'plus'} iconClass={'me-0'}/>
                        </Button>
                    </Col>
                </Row>
                :
                <Button variant='light-primary' className={`w-100 box-shadow`}  onClick={()=>openContent(item, index)} disabled={localStorage.getItem('wallLimitExceed') == 'true' ? true : false}>
                    <FontAwesomeIcon type={'solid'} icon={'tag'} iconClass={'me-1'} />Tag Products
                </Button>
        )
    }
}


const mapStateToProps = state => {
    return { moderation: state.moderation, }
}
const mapDispatchToProps = dispatch => {
    return { REMOVE_TAGGED_PRODUCT_ACTION: (product_sku, productId, referenceId, navigate) => dispatch(REMOVE_TAGGED_PRODUCT_ACTION(product_sku, productId, referenceId)), }
}

export default connect(mapStateToProps, mapDispatchToProps)(ProductTags)