import React, { Component } from 'react'
import { connect } from "react-redux";
import parse from 'react-html-parser';
// import { generateSocialName } from '../../Feeds/utils'
import { GET_NETWORK_ICON } from '../../../../../utils';
import PostTagSearch from './postTagSearch'
import { updateFilterPostTags, updateFilterSocialFeeds, updateFilterPostType, updateFilterOthers, removeFilterSocialFeeds, removeFilterPostType, removeFilterOthers, removeFilterPostTags, removeAllFilterBody } from '../../../../../actions/moderationActions'
import { Accordion, Offcanvas, Button, Image, Form } from 'react-bootstrap';
import { baseUrl, FontAwesomeIcon } from '../../../../../Assets/AssetsHelper';


const PostTypeCheck = ({ value, onClickPostType, moderation, label, id }) => {
    const selecteds = Object.keys(moderation.filterBody).length > 0 && moderation.filterBody.postType.length > 0 ? moderation.filterBody.postType.filter((fin) => fin == value) : []
    const isChecked = selecteds && selecteds.length > 0 ? true : false
    return <Form.Check type="checkbox" className='mb-4' value="1" label={label} id={id} onChange={onClickPostType(value, isChecked)} checked={isChecked} />
}

const OtherInput = ({ value, onClickOther, moderation, label, id }) => {
    const selecteds = Object.keys(moderation.filterBody).length > 0 && moderation.filterBody.other.length > 0 ? moderation.filterBody.other.filter((fin) => fin.key == id) : []
    const isChecked = selecteds && selecteds.length > 0 ? true : false
    return <Form.Check type="checkbox" className='mb-4' value="1" label={label} id={id} onChange={onClickOther(id, value, isChecked)} checked={isChecked}  />
}


class FilterBody extends Component {
    constructor(props) {
        super(props);
        this.state = { socialFeeds: [], postType: [], other: [], feeds: [], isActiveAside: false }
        this.toogleFilter = this.toogleFilter.bind(this);
    }
   

    toogleFilter = () => {
        this.setState({ isActiveAside: !this.state.isActiveAside, })
        setTimeout(() => {
            var offcanvas = document.querySelector(".offcanvas-backdrop");
            if (offcanvas) {
                offcanvas.classList.add('filter-backdrop');
            }
        }, 100);
    };

    onSelectFeeds = (key, value, status) => event => {
        if (!status) this.props.updateFilterSocialFeeds({ key, value })
        else this.props.removeFilterSocialFeeds({ key, value })
    }
    onClickPostType = (value, status) => event => {
        if (!status) this.props.updateFilterPostType(value)
        else this.props.removeFilterPostType(value)
    }
    onClickOther = (key, value, status) => event => {
        if (!status) this.props.updateFilterOthers({ key, value })
        else this.props.removeFilterOthers({ key, value })
    }
    componentDidMount() {
        this.addEventListeners(); 

        if (window.feedId && window.feedId != "") {
            const { feeds } = this.props 
            feeds && feeds.map((item, index) => { if (window.feedId == item.id) { this.props.updateFilterSocialFeeds({ key: item.name, value: window.feedId }) } })
        }
    }
    
    componentDidUpdate() { this.addEventListeners(); }

    addEventListeners() {
        const filterAside = document.getElementById("filter_aside");
        if (filterAside) {
            filterAside.addEventListener("click", this.toogleFilter);
        }
    }
    
    render() {
        const { moderation, feeds} = this.props
        const { isActiveAside } = this.state
        const UserRuleEnableAI = false;
        const UserRuleManualTag = true;
        const postTags = moderation?.filterBody?.postTags;
        

        return <Offcanvas show={isActiveAside} placement={'end'} onHide={this.toogleFilter} className="filter_aside">
            <Offcanvas.Body>
                <div className="f-between px-4 py-3 border-bottom">
                    <span className="fs-6 fw-medium">Add Filter</span>
                    <Button variant='icon' size='sm' className='btn-active-primary' onClick={this.toogleFilter}>
                        <FontAwesomeIcon icon={'xmark'}/>
                    </Button>
                </div>
                {(UserRuleEnableAI || UserRuleManualTag) ?
                    <Accordion className="border-bottom" flush>
                        <Accordion.Item defaultactivekey="0">
                            <Accordion.Header>
                                <span className="text-uppercase fs-8 fw-medium">Post Tags</span>
                            </Accordion.Header>
                            <Accordion.Body className="px-5 py-1">
                                {UserRuleEnableAI &&
                                    <Image className='mb-3' src={baseUrl('media/svg/trusense.svg')} height={23} width={120} fluid/>
                                }
                                <div id="tag_post_btn" className="collapse show" data-parent="#tagspost">
                                    <PostTagSearch postTags={postTags} {...this.props} />
                                </div>
                            </Accordion.Body>
                        </Accordion.Item>
                    </Accordion>
                    : null}
                <Accordion className="border-bottom" flush>
                    <Accordion.Item defaultactivekey="0">
                        <Accordion.Header>
                            <span className="text-uppercase fs-8 fw-medium">Social Feeds</span>
                        </Accordion.Header>
                        <Accordion.Body className="px-5 py-1">
                            <div id="social_feed_btn" className="collapse show" data-parent="#post_type_drop">
                                <div className="checkbox-list">
                                    {
                                        feeds && feeds.map((item, index) => {
                                            const selecteds = Object.keys(moderation.filterBody).length > 0 && moderation.filterBody.socialFeed.length > 0 ? moderation.filterBody.socialFeed.filter((fin) => fin.value == item.id) : []
                                            const isChecked = selecteds && selecteds.length > 0 ? true : false
                                            return item.status ? <Form.Label key={index} className='d-flex align-items-start mb-4' htmlFor={`moderationfeed${item.id}`}>
                                                    <Form.Check type="checkbox" id={`moderationfeed${item.id}`} value={item.id} data-filter={item.filterId} onChange={this.onSelectFeeds(item.name, item.id, isChecked)} checked={isChecked} style={{marginTop:2}}/>
                                                    <Image className='mx-2' src={baseUrl(`media/icons/social-icons/${GET_NETWORK_ICON(item.networkId)}.svg`)} height={20} width={20} fluid/>
                                                    <span className='fil_network'>{parse(item.name)}</span>
                                                </Form.Label> :''
                                        })
                                    }

                                </div>
                            </div>
                        </Accordion.Body>
                    </Accordion.Item>
                </Accordion>

                <Accordion className="border-bottom" flush>
                    <Accordion.Item defaultactivekey="0">
                        <Accordion.Header>
                            <span className="text-uppercase fs-8 fw-medium">Post Type</span>
                        </Accordion.Header>
                        <Accordion.Body className="px-5 py-1">
                            <PostTypeCheck value="1" onClickPostType={this.onClickPostType} {...this.props} label={`Text Only`} id={`text_only`}/>
                            <PostTypeCheck value="2,4" onClickPostType={this.onClickPostType} moderation={moderation} label={`With Image`} id={`image_only`}/>
                            <PostTypeCheck value="3,5" onClickPostType={this.onClickPostType} moderation={moderation} label={`With Video`} id={`video_only`}/>
                        </Accordion.Body>
                    </Accordion.Item>
                </Accordion>

                <Accordion className="border-bottom" flush>
                    <Accordion.Item defaultactivekey="0">
                        <Accordion.Header>
                            <span className="text-uppercase fs-8 fw-medium">Other</span>
                        </Accordion.Header>
                        <Accordion.Body className="px-5 py-1">
                            <OtherInput value="1" onClickOther={this.onClickOther} moderation={moderation} label={`Pinned to top`} id={`pinFilter`}/>
                            <OtherInput value="1" onClickOther={this.onClickOther} moderation={moderation} label={`Recent`} id={`recentFilter`} />
                            <OtherInput value="1" onClickOther={this.onClickOther} moderation={moderation} label={`Retweets`} id={`retweetsFilter`} />
                        </Accordion.Body>
                    </Accordion.Item>
                </Accordion>
                <div className="d-flex flex-column mx-5 py-4">
                    <Button variant='link' onClick={this.props.removeAllFilterBody} className="">Reset Results</Button>
                </div>
            </Offcanvas.Body>
        </Offcanvas>

    }
}
const mapStateToProps = (state) => {
    return {
         moderation: state.moderation ,
         feeds: state.manageFeeds?.feeds
        }
}
const mapDispatchToProps = dispatch => {
    return {
        updateFilterPostTags: (data) => dispatch(updateFilterPostTags(data)),
        updateFilterSocialFeeds: (data) => dispatch(updateFilterSocialFeeds(data)),
        updateFilterPostType: (data) => dispatch(updateFilterPostType(data)),
        updateFilterOthers: (data) => dispatch(updateFilterOthers(data)),
        removeFilterSocialFeeds: (data) => dispatch(removeFilterSocialFeeds(data)),
        removeFilterPostType: (data) => dispatch(removeFilterPostType(data)),
        removeFilterOthers: (data) => dispatch(removeFilterOthers(data)),
        removeFilterPostTags: (data) => dispatch(removeFilterPostTags(data)),
        removeAllFilterBody: () => dispatch(removeAllFilterBody()),
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(FilterBody);