import React, { Component } from 'react'
import { connect } from "react-redux";
import { FontAwesomeIcon } from '../../../../../Assets/AssetsHelper';
import { Form, Button, Badge } from 'react-bootstrap';
import { removeFilterSocialFeeds, removeFilterPostType, removeFilterOthers, removeFilterPostTags, removeDateRanges, removeAllFilterBody } from '../../../../../actions/moderationActions'
import HtmlParser from 'react-html-parser';
class FilterTab extends Component {
    onSelectFeeds = (data) => event => { this.props.removeFilterSocialFeeds(data) }
    onClickPostType = (value) => event => { this.props.removeFilterPostType(value) }
    onClickOther = (data) => event => { this.props.removeFilterOthers(data) }
    onClickRemovePostTags = (value) => event => { this.props.removeFilterPostTags(value) }
    onClickToDateRangeRemove = event => { this.props.removeDateRanges() }

    render() {
        const { filters, onClickReset, filterBody } = this.props
        return (filterBody.socialFeed.length > 0 || filterBody.postType.length > 0 || filterBody.postTags.length > 0 || filterBody.other.length > 0 || Object.keys(filterBody.dateRanges).length > 0) ? <div className="d-flex align-items-start mb-2">
            <span className="text-muted fw-medium me-2 my-2 text-nowrap">Filter By : </span>
            <div className="d-flex flex-wrap align-items-center">
                {
                    (Object.keys(filterBody.dateRanges).length > 0) ? <Badge bg='gray-200' className="rounded-pill badge-light-secondary">
                        {`${filterBody.dateRanges.startDate} - ${filterBody.dateRanges.endDate}`}
                        <Button variant='icon' className='p-0 bg-gray-200 rounded-circle border-0 h-18px w-18px ms-2' onClick={this.onClickToDateRangeRemove}>
                            <FontAwesomeIcon type={'solid'} icon={'xmark'}/>
                        </Button>
                    </Badge> : null
                }
                {
                    filterBody.postTags && filterBody.postTags.map((sItem) => {
                        return <Badge bg='gray-200' className="rounded-pill badge-light-secondary">
                            {sItem}
                            <Button variant='icon' className='p-0 bg-gray-200 rounded-circle border-0 h-18px w-18px ms-2' onClick={this.onClickRemovePostTags(sItem)}>
                                <FontAwesomeIcon type={'solid'} icon={'xmark'}/>
                            </Button>
                        </Badge>
                    })
                }
                {
                    filterBody.socialFeed && filterBody.socialFeed.map((sItem) => {
                        return <Badge bg='gray-200' className="rounded-pill badge-light-secondary text-icon">
                            {sItem.key == "onsiteupload" ? "SnapUp" : HtmlParser(sItem.key)}
                            <Button variant='icon' className='p-0 bg-gray-200 rounded-circle border-0 h-18px w-18px ms-2' onClick={this.onSelectFeeds(sItem)}>
                                <FontAwesomeIcon type={'solid'} icon={'xmark'}/>
                            </Button>
                        </Badge>
                    })
                }

                {
                    filterBody.postType && filterBody.postType.map((sItem) => {
                        if (sItem == "1") {
                            return <Badge bg='gray-200' className="rounded-pill badge-light-secondary">
                                Text Only
                                <Button variant='icon' className='p-0 bg-gray-200 rounded-circle border-0 h-18px w-18px ms-2' onClick={this.onClickPostType(sItem)}>
                                    <FontAwesomeIcon type={'solid'} icon={'xmark'}/>
                                </Button>
                            </Badge>
                        }
                        else if (sItem == "2,4") {
                            return <Badge bg='gray-200' className="rounded-pill badge-light-secondary">
                                With Image
                                <Button variant='icon' className='p-0 bg-gray-200 rounded-circle border-0 h-18px w-18px ms-2' onClick={this.onClickPostType(sItem)}>
                                    <FontAwesomeIcon type={'solid'} icon={'xmark'}/>
                                </Button>
                            </Badge>
                        }
                        else if (sItem == "3,5") {
                            return <Badge bg='gray-200' className="rounded-pill badge-light-secondary">
                                With Video
                                <Button variant='icon' className='p-0 bg-gray-200 rounded-circle border-0 h-18px w-18px ms-2' onClick={this.onClickPostType(sItem)}>
                                    <FontAwesomeIcon type={'solid'} icon={'xmark'}/>
                                </Button>
                            </Badge>
                        }
                    })
                }

                {
                    filterBody.other && filterBody.other.map((sItem) => {
                        if (sItem.key == "highlightFilter") {
                            return <Badge bg='gray-200' className="rounded-pill badge-light-secondary">
                                Highlight Post
                                <Button variant='icon' className='p-0 bg-gray-200 rounded-circle border-0 h-18px w-18px ms-2' onClick={this.onClickOther(sItem)}>
                                    <FontAwesomeIcon type={'solid'} icon={'xmark'}/>
                                </Button>
                            </Badge>
                        }
                        else if (sItem.key == "pinFilter") {
                            return <Badge bg='gray-200' className="rounded-pill badge-light-secondary">
                                Pin Post
                                <Button variant='icon' className='p-0 bg-gray-200 rounded-circle border-0 h-18px w-18px ms-2' onClick={this.onClickOther(sItem)}>
                                    <FontAwesomeIcon type={'solid'} icon={'xmark'}/>
                                </Button>
                            </Badge>
                        }
                        else if (sItem.key == "recentFilter") {
                            return <Badge bg='gray-200' className="rounded-pill badge-light-secondary">
                                Recent Post
                                <Button variant='icon' className='p-0 bg-gray-200 rounded-circle border-0 h-18px w-18px ms-2' onClick={this.onClickOther(sItem)}>
                                    <FontAwesomeIcon type={'solid'} icon={'xmark'}/>
                                </Button>
                            </Badge>
                        }
                        else if (sItem.key == "retweetsFilter") {
                            return <Badge bg='gray-200' className="rounded-pill badge-light-secondary">
                                Retweets Post
                                <Button variant='icon' className='p-0 bg-gray-200 rounded-circle border-0 h-18px w-18px ms-2' onClick={this.onClickOther(sItem)}>
                                    <FontAwesomeIcon type={'solid'} icon={'xmark'}/>
                                </Button>
                            </Badge>
                        }
                    })
                }
                <Button variant='dark' className='rounded-pill py-1 m-1' onClick={this.props.removeAllFilterBody}>Reset</Button>
            </div>
        </div> : null
    }
}

const mapStateToProps = (state) => {
    return { moderation: state.moderation }
}
const mapDispatchToProps = dispatch => {
    return {
        removeFilterSocialFeeds: (data) => dispatch(removeFilterSocialFeeds(data)),
        removeFilterPostType: (data) => dispatch(removeFilterPostType(data)),
        removeFilterOthers: (data) => dispatch(removeFilterOthers(data)),
        removeFilterPostTags: (data) => dispatch(removeFilterPostTags(data)),
        removeDateRanges: () => dispatch(removeDateRanges()),
        removeAllFilterBody: () => dispatch(removeAllFilterBody()),
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(FilterTab);